p {
  font-family: Protest Revolution;
}

.cat-info {
  width: 100%;
  height: 100%;
}

.cat-info img {
  width: 360px;
  height: 400px;
  margin-top: 10px;
}

.loader {
  width: 84px;
  height: 84px;
  position: relative;
  top: 300px;
  left: 50%;
  right: 0;
}

.loader:before, .loader:after {
  content: "";
  width: 84px;
  height: 84px;
  background: #ff3d00;
  border-radius: 50%;
  animation: push 1s linear infinite alternate;
  position: absolute;
  top: 0;
  right: 0;
}

.loader:after {
  background: #fff;
  animation-direction: alternate-reverse;
  top: auto;
  bottom: 0;
  left: 0;
}

@keyframes push {
  0% {
    width: 14px;
    height: 14px;
  }

  100% {
    width: 84px;
    height: 84px;
  }
}

